import { axios } from "./axios";

const getAllGroups = () => {
  return axios
    .get("/api/reports/groups/")
    .then((response) => response.data && response.data.groups);
};
const getAllRobins = () => {
  return axios
    .get("/api/reports/robins")
    .then((response) => response.data && response.data.robins);
};
const getAllEvents = () => {
  return axios
    .get("/api/reports/events/")
    .then((response) => response.data && response.data.events);
};
const getAllDrives = () => {
  return axios
    .get("/api/reports/drives")
    .then((response) => response.data && response.data.drives);
};

const getAttendanceReport = (query) => {
  const url = `/api/reports/attendance?${new URLSearchParams(
    query
  ).toString()}`;
  return axios
    .get(url)
    .then((response) =>
      response && response.data ? response.data.report : []
    );
};

export {
  getAllGroups,
  getAllRobins,
  getAllEvents,
  getAllDrives,
  getAttendanceReport,
};
