import { useState } from "react";
import { Form, Select } from "semantic-ui-react";
import { GreenButton } from "../../GreenButton";

const AttendanceRobinFilters = ({
  groups,
  onGroupSelect,
  onShowEventSelector,
}) => {
  const options = [
    { key: -1, text: "Select Group", value: "-1", selected: true },
    ...groups.map((group, index) => ({
      key: index,
      text: group.title,
      value: group._id,
    })),
  ];

  const [selectedGroup, setSelectedGroup] = useState("-1");

  const handleOnChange = (event, { value }) => {
    setSelectedGroup(value);
    onGroupSelect(event, { value });
  };

  return (
    <div className="border rounded p-2">
      <div class="d-flex flex-wrap">
        <div class="mx-3">
          <div className="">
            <div className="mt-4 mb-4">
              <GreenButton onClick={onShowEventSelector}>
                Select Event
              </GreenButton>
            </div>
          </div>
        </div>
        <div class="mx-3">
          <Form className="col">
            <Form.Field>
              <label>Select Group</label>
              <Select
                onChange={handleOnChange}
                name={"select"}
                options={options}
                value={selectedGroup}
              ></Select>
            </Form.Field>
          </Form>
        </div>
      </div>
    </div>
  );
};

export { AttendanceRobinFilters };
