import { Route, Switch, useHistory, useRouteMatch } from "react-router-dom";
import { DashboardOptions } from "../ui/DashboardOptions";
import { dashBoardCardOptions } from "./config";

const DashBoard = (props) => {
  const history = useHistory();

  const onCardClick = (url) => {
    history.push(url);
  };

  const { path } = useRouteMatch();
  return (
    <div className="container-fluid">
      <Switch>
        {dashBoardCardOptions.map(({ url, Component }, index) => (
          <Route path={url} key={index}>
            <Component></Component>
          </Route>
        ))}
        <Route path={path} exact>
          <DashboardOptions
            {...{ onCardClick, dashBoardCardOptions }}
          ></DashboardOptions>
        </Route>
      </Switch>

      <br />
      <br />
      <br />
      <br />
    </div>
  );
};

export { DashBoard };
