import { Fragment, useEffect, useState } from "react";
import { Tab } from "semantic-ui-react";
import { getAllGroups, deleteGroup } from "../../../../services/group-service";
import { ConfirmationDialog } from "../../common/ConfirmationDialog";
import { AllGroups } from "./AllGroups";
import { GroupForm } from "./GroupForm";
const GroupPage = () => {
  const [groups, setGroups] = useState([]);

  useEffect(() => {
    getAllGroups().then((groups) => {
      setGroups(groups);
    });
  }, []);

  const [groupToDelete, setGroupToDelete] = useState(null);
  const [confirmDialog, setConfirmDialog] = useState({
    show: false,
    title: "Delete Group",
    positiveAction: "Yes",
    negativeAction: "Cancel",
  });

  const confirmDelete = (group) => {
    setGroupToDelete(group);
    setConfirmDialog({ ...confirmDialog, show: true });
  };

  const handleDeleteGroup = () => {
    console.log("Dleeting ", groupToDelete);
    console.error("Deletion is Pending...");

    deleteGroup(groupToDelete._id)
      .then(() => {
        const index = groups.findIndex(
          (group) => group._id === groupToDelete._id
        );
        const copy = [...groups];
        copy.splice(index, 1);
        setGroups(copy);
        setConfirmDialog({ ...confirmDialog, show: false });
      })
      .catch((err) => {
        setConfirmDialog({ ...confirmDialog, show: false });

        throw new Error(err);
      });
  };

  const tabs = [
    {
      menuItem: "All Groups",
      render: () => (
        <Tab.Pane>
          <AllGroups groups={groups} onDelete={confirmDelete}></AllGroups>
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Add Group",
      render: () => (
        <Tab.Pane>
          <GroupForm></GroupForm>
        </Tab.Pane>
      ),
    },
  ];

  return (
    <Fragment>
      <ConfirmationDialog
        {...confirmDialog}
        positiveActionHandler={handleDeleteGroup}
        negativeActionHandler={() =>
          setConfirmDialog({ ...confirmDialog, show: false })
        }
      >
        Do You Really Want To Delete The Group?
      </ConfirmationDialog>
      <div className="container mt-3">
        <Tab panes={tabs} />
      </div>
    </Fragment>
  );
};

export { GroupPage };
