import { Fragment, useState } from "react";
import { Divider, Form } from "semantic-ui-react";
import { createGroup } from "../../../../services/group-service";
import { GreenButton } from "../../GreenButton";

const GroupForm = () => {
  const [group, setGroup] = useState({ title: "" });
  const handleFormSubmit = () => {
    createGroup(group).then((group) => {
      alert("Group Created.");
      setGroup({ title: "" });
    });
  };

  const onInputChange = (event) => {
    console.log("group");
    setGroup({
      [event.target.name]: event.target.value,
    });
  };
  return (
    <Fragment>
      <div className="container mt-3">
        <div className="conatiner p-4">
          <div className="mt-4">
            <div>
              <div className="">
                <div>
                  <p className="display-6">Add New Group</p>
                  <Divider></Divider>
                </div>
                <Form onSubmit={handleFormSubmit}>
                  <Form.Field>
                    <label>Group Name</label>
                    <input
                      required
                      placeholder="Name"
                      onChange={onInputChange}
                      value={group.title}
                      name="title"
                    />
                  </Form.Field>
                  <br />
                  <GreenButton type="submit">Save group</GreenButton>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export { GroupForm };
