import { useContext, useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { AuthContext } from "../../App";

const Navbar = () => {
  let { REACT_APP_BRAND_COLOR } = process.env;

  const style = {
    backgroundColor: REACT_APP_BRAND_COLOR,
  };

  const { onLogout, loggedInUser } = useContext(AuthContext);
  const [isAdmin, setIsAdmin] = useState(false);
  const history = useHistory();

  useEffect(() => {
    setIsAdmin(loggedInUser && loggedInUser.type === "ADMIN");
  }, [loggedInUser]);

  const logout = () => {
    localStorage.clear();
    onLogout();
    history.push("/login");
  };

  return (
    <nav style={style} className="navbar navbar-expand-lg navbar-dark">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">
          <img src="/images/logo.jpeg" alt="logo" height="40" />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link active" to="/">
                Home
              </Link>
            </li>

            {isAdmin && (
              <li className="nav-item">
                <Link className="nav-link " to="/dashboard">
                  Dashboard
                </Link>
              </li>
            )}
            {loggedInUser ? (
              <li className="nav-item" onClick={logout}>
                <a className="nav-link" href="#">
                  Logout
                </a>
              </li>
            ) : (
              <li className="nav-item">
                <Link className="nav-link " to="/login">
                  Login
                </Link>
              </li>
            )}
          </ul>
        </div>
      </div>
    </nav>
  );
};

export { Navbar };
