import React, { Fragment, useEffect, useState } from "react";
import { Divider } from "semantic-ui-react";
import { getAllGroups } from "../../../../services/group-service";
import {
  deleteBulkRobins,
  getAllRobins,
  makeRobinsAdmin,
  makeRobinsVolunteer,
  moveBulkRobinsToGroup,
} from "../../../../services/robin-service";
import { ConfirmationDialog } from "../../common/ConfirmationDialog";
import { MessageDialog } from "../../common/MessageDialog";
import { Actions } from "./Actions";
import { RobinFilterModal } from "./RobinFilterModal";
import { ViewSwitcher } from "./ViewSwitcher";
const AllRobinContext = React.createContext();
const AllRobins = () => {
  const [robins, setRobins] = useState([]);
  const [view, setViews] = useState("table");
  const [groups, setGroups] = useState([]);
  const [selectedRobins, setSelectedRobins] = useState([]);
  const [filterModal, setFilterModal] = useState({ show: false });
  const [alert, setAlert] = useState({
    body: "Done",
    show: false,
    title: "Message",
  });
  const [confirm, setConfirm] = useState({
    show: false,
    title: "Deleting Robin",
    positiveAction: "Delete",
    negativeActionHandler: () => setConfirm({ ...confirm, show: false }),
  });
  const [actionChangeData, setActionChangeData] = useState({
    event: {},
    data: {},
  });

  // [{key , value, text}]
  const [bulkOptions, setBulkOptions] = useState(
    [
      { value: "", text: "Select An Option" },
      { value: "select-all", text: "Select All" },
      { value: "unselect-all", text: "Unselected All" },
      { value: "make-selected-admin", text: "Make Selected Admin" },
      { value: "make-selected-volunteer", text: "Make Selected Volunteer" },
      { value: "delete-selected", text: "Delete" },
    ].map((option, index) => ({ ...option, key: index }))
  );

  useEffect(() => {
    getAllRobins().then((robins) => {
      robins = robins.map((robin) => ({
        selected: false,
        robin,
      }));
      setRobins(robins);
      console.log(robins);
    });
  }, []);
  useEffect(() => {
    getAllGroups().then((groups) => {
      console.log(groups);
      setGroups(groups);
      const groupactions = groups.map((group, index) => ({
        key: group._id + index,
        text: `Move To ${group.title}`,
        value: `move-to-group-${group._id}`,
      }));

      setBulkOptions([...bulkOptions, ...groupactions]);
    });
  }, []);

  const handleClick = (robin) => {
    console.log(robin);
  };

  const selectRobin = (index) => {
    console.log("Robin Selected", index);
    const updated = [...robins];
    const selectedRobin = updated[index];

    selectedRobin.selected = !selectedRobin.selected;

    setRobins(updated);

    const selectedRobinsCopy = [...selectedRobins];
    if (selectedRobin.selected) {
      setSelectedRobins([...selectedRobinsCopy, selectedRobin.robin]);
    } else {
      const index = selectedRobins.findIndex(
        (r) => r._id === selectedRobin.robin._id
      );
      selectedRobinsCopy.splice(index, 1);
      setSelectedRobins([...selectedRobinsCopy]);
    }
  };

  const onBulkOptionChange = (event, data) => {
    console.log({ event, data });
    setActionChangeData({ event, data });
  };

  function changeSelectionOfAll(select = true) {
    const rbs = robins.map((r) => ({ ...r, selected: select }));
    setRobins(rbs);
    setSelectedRobins(select ? [...rbs.map((rbs) => rbs.robin)] : []);
  }

  function deleteSelectedRobins() {
    const ids = selectedRobins.map((robin) => robin._id);
    deleteBulkRobins(ids).then((data) => {
      setSelectedRobins([]);
      const copy = [...robins];
      ids.forEach((id) => {
        const index = copy.findIndex((obj) => obj.robin._id === id);
        copy.splice(index, 1);
      });

      setRobins([...copy]);

      setConfirm({ ...confirm, show: false });

      setAlert({
        ...alert,
        show: true,
        body: `${ids.length} Robins Deleted.`,
        title: "Deletion Success",
      });
    });
  }

  const showConfirmDialogForDeletion = () => {
    setConfirm({
      ...confirm,
      show: true,
      title: "Bulk Delete",
      body: (
        <div>
          <p>Do you really want to delete {selectedRobins.length} Robins.</p>
          <p color="red">You cant undo after delete!!</p>
        </div>
      ),
      positiveActionHandler: () => {
        deleteSelectedRobins();
      },
    });
  };

  const showErrorMessage = (message) => {
    setAlert({
      ...alert,
      show: true,
      isError: true,
      body: `Something Went Wrong !`,
      title: "Operation Failed.",
    });
  };

  const makeSelectedAdmin = () => {
    const ids = selectedRobins.map((robin) => robin._id);
    const copy = [...robins];
    makeRobinsAdmin(ids).then((data) => {
      copy.forEach(({ robin }) => {
        if (ids.includes(robin._id)) {
          robin.type = "ADMIN";
        }
      });
      setRobins(copy);
      setAlert({
        ...alert,
        show: true,
        isError: false,
        body: `${ids.length} Are changed to Admin.`,
        title: "Operation Success",
      });
    });
  };
  const makeSelectedVolunteer = () => {
    const ids = selectedRobins.map((robin) => robin._id);
    const copy = [...robins];
    makeRobinsVolunteer(ids).then((data) => {
      copy.forEach(({ robin }) => {
        if (ids.includes(robin._id)) {
          robin.type = "VOLUNTEER";
        }
      });
      setRobins(copy);

      setAlert({
        ...alert,
        show: true,
        isError: false,
        body: `${ids.length} Are changed to Volunteer.`,
        title: "Operation Success",
      });
    });
  };

  const moveToGroup = (group) => {
    console.log("Moving ", robins.length, "Robins to ", group);
    const ids = selectedRobins.map((robin) => robin._id);
    moveBulkRobinsToGroup(group._id, ids).then((data) => {
      const copy = [...robins];

      console.log(ids);
      copy.forEach(({ robin }) => {
        if (ids.includes(robin._id)) {
          console.log("found..");
          robin.group = group;
        }
      });

      setRobins(copy);
    });
  };

  const performBulkAction = () => {
    const { event, data } = actionChangeData;

    const actions = {
      "select-all": () => changeSelectionOfAll(true),
      "unselect-all": () => changeSelectionOfAll(false),
      "delete-selected": showConfirmDialogForDeletion,
      "make-selected-admin": makeSelectedAdmin,
      "make-selected-volunteer": makeSelectedVolunteer,
      ...groups.reduce((reduced, group) => {
        reduced[`move-to-group-${group._id}`] = () => moveToGroup(group);
        return reduced;
      }, {}),
    };

    if (!data) return;

    if (actions[data.value]) {
      actions[data.value]();
    }

    console.log(
      "Performing ",
      data.value,
      "On ",
      selectedRobins.length,
      "Robins"
    );
  };

  const hideFilterModel = () => {
    setFilterModal({ ...filterModal, show: false });
  };

  const showFilterModal = () => {
    setFilterModal({ ...filterModal, show: true });
    console.log("Hide Filter");
  };
  return (
    <AllRobinContext.Provider value={robins}>
      <Fragment>
        <RobinFilterModal
          {...filterModal}
          onHide={hideFilterModel}
        ></RobinFilterModal>
        <ConfirmationDialog {...confirm}>{confirm.body}</ConfirmationDialog>
        <MessageDialog
          {...alert}
          onOk={() => setAlert({ ...alert, show: false })}
        >
          {alert.body}
        </MessageDialog>

        <div className="container-fluid">
          <div className="mt-3 mx-auto">
            <div>
              <h1 className="display-5">All Robins</h1>
            </div>
            <Divider></Divider>
            <Actions
              {...{
                onBulkOptionChange,
                bulkOptions,
                onViewChange: setViews,
                performBulkAction,
                onShowFilterModal: showFilterModal,
              }}
            ></Actions>
          </div>

          <div className="mt-4">
            <ViewSwitcher
              {...{
                onClick: handleClick,
                onSelect: selectRobin,
                view: view,
              }}
            ></ViewSwitcher>
          </div>
        </div>
      </Fragment>
    </AllRobinContext.Provider>
  );
};

export { AllRobins, AllRobinContext };
