import { appendQueryString } from "../utils";
import { axios } from "./axios";

const createRobin = (robin) => {
  return axios.post("/api/robins", robin);
};
const getAllRobins = (filters) => {
  const url = appendQueryString("/api/robins/", filters);
  return axios
    .get(url)
    .then((response) => response.data && response.data.robins);
};

const loginRobin = ({ email, password }) => {
  console.log({ email, password });
  return axios.post("/api/robins/login", { email, password });
};
const deleteBulkRobins = (ids) => {
  console.log(ids);
  return axios
    .delete("/api/robins/bulk", { data: { robins: ids } })
    .then((response) => response.data);
};
const moveBulkRobinsToGroup = (group, ids) => {
  console.log({ group, ids });
  return axios
    .post("/api/robins/bulk/move/group/", { robins: ids, group })
    .then((response) => response.data);
};

const makeRobinsAdmin = (ids) => {
  return axios
    .post("/api/robins/bulk/change/type/admin", { robins: ids })
    .then((response) => response.data);
};
const makeRobinsVolunteer = (ids) => {
  return axios
    .post("/api/robins/bulk/change/type/volunteer", { robins: ids })
    .then((response) => response.data);
};

export {
  createRobin,
  loginRobin,
  getAllRobins,
  deleteBulkRobins,
  makeRobinsAdmin,
  makeRobinsVolunteer,
  moveBulkRobinsToGroup,
};
