import { Fragment } from "react";
import { Message } from "semantic-ui-react";
import { useEffect, useState } from "react";
import {
  getAttendanceOfEvent,
  markAttendance,
} from "../../../services/attendance-service";
import { getAllDrives } from "../../../services/drive-service";
import { getAllEvents } from "../../../services/event-service";
import { getAllGroups } from "../../../services/group-service";
import { getAllRobins } from "../../../services/robin-service";
import { AttendanceMarker } from "./attendance/AttendanceMarker";
import { AttendanceRobinFilters } from "./attendance/AttendanceRobinFilter";
import { EventSelector } from "./attendance/EventSelector";
import { formatWithAMandPM } from "../../../utils";
import toTitleCase from "titlecase";
import id from "date-fns/esm/locale/id/index.js";
import { isEmpty } from "lodash";

const MarkAttendance = () => {
  const [drives, setDrives] = useState([]);
  const [robins, setRobins] = useState([]);
  const [filteredRobins, setFilteredRobins] = useState([]);
  const [groups, setGroups] = useState([]);
  const [events, setEvents] = useState([]);
  const [showEventSelector, setShowEventSelector] = useState(false);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [filters, setFilters] = useState({});
  const [attendances, setAttendances] = useState([]);
  const [eventFilters, setEventFilter] = useState({});

  useEffect(() => {
    if (selectedEvent)
      getAttendanceOfEvent(selectedEvent._id).then((response) => {
        setAttendances(response);
      });
  }, [selectedEvent]);

  useEffect(() => {
    console.log({ filters });
    getAllRobins(filters).then((robins) => {
      setRobins(robins || []);
    });
  }, [filters]);

  useEffect(() => {
    getAllGroups().then((groups) => {
      setGroups(groups);
    });

    getAllDrives().then((drives) => {
      setDrives(drives);
    });
  }, []);

  useEffect(() => {
    getAllEvents(eventFilters).then((events) => {
      setEvents(events);
    });
  }, [eventFilters]);

  const handleEventSelect = (event) => {
    setSelectedEvent(event);
    setShowEventSelector(false);
  };

  const handleGroupSelect = (event, data) => {
    const { value } = data;
    const copy = { ...filters };
    !value ? delete copy.group : (copy.group = value);

    setFilters(copy);
  };

  const onEventFilterChange = (filter) => {
    setEventFilter({ ...eventFilters, ...filter });
  };

  const handleMarkAttandance = ({ event, data, drive, robin, index }) => {
    if (!selectedEvent) {
      alert("select an event first ");
      return;
    }

    const checked = data.checked;
    markAttendance({
      checked,
      drive: drive._id,
      robin: robin._id,
      event: selectedEvent._id,
    })
      .then((attendance) => {
        const attendanceOfRobin = attendances.find(
          (at) => at.robin === robin._id
        );

        const attendancesCopy = [...attendances];
        // update attendnace
        if (attendanceOfRobin) {
          attendanceOfRobin[drive._id] = checked;
        } else {
          // cerate new
          attendancesCopy.push({
            [drive._id]: checked,
            robin: robin._id,
            event: selectedEvent._id,
          });
        }

        setAttendances(attendancesCopy);
      })
      .catch((error) => {
        console.error(error);
        return;
      });
  };

  const renderEventSelector = () => (
    <EventSelector
      selectedEvent={selectedEvent}
      show={showEventSelector}
      onSelect={handleEventSelect}
      events={events}
      onClose={() => setShowEventSelector(false)}
      onEventFilterChange={onEventFilterChange}
    ></EventSelector>
  );

  const renderAttendanceMarker = () => {
    return (
      selectedEvent && (
        <AttendanceMarker
          onMarkAttendance={handleMarkAttandance}
          drives={drives}
          robins={robins}
          attendances={attendances}
        ></AttendanceMarker>
      )
    );
  };

  const renderInfoMessageAfterEventSelect = () => (
    <Message success size="large">
      <Message.Header>
        <p>
          {toTitleCase(`Marking Attendance Of Event Which is/was on`)}
          <b>{` ${new Date(
            selectedEvent.date
          ).toDateString()} ${formatWithAMandPM(
            selectedEvent.time.hr,
            selectedEvent.time.min
          )}`}</b>
        </p>
      </Message.Header>
    </Message>
  );

  const renderSelectAnEventErrorMessage = () => (
    <Message warning size="large">
      <Message.Header>
        <p>
          {toTitleCase(
            `please Select An Event to mark attendance ! Click on Select event to continue`
          )}
        </p>
      </Message.Header>
    </Message>
  );

  const renderAttendanceFilters = () => {
    return (
      <AttendanceRobinFilters
        onShowEventSelector={setShowEventSelector}
        onGroupSelect={handleGroupSelect}
        groups={groups}
      ></AttendanceRobinFilters>
    );
  };

  return (
    <Fragment>
      <div className="m-4">
        {renderAttendanceFilters()}

        {selectedEvent
          ? renderInfoMessageAfterEventSelect()
          : renderSelectAnEventErrorMessage()}

        {renderEventSelector()}
        {renderAttendanceMarker()}
      </div>
    </Fragment>
  );
};

export { MarkAttendance };
