import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.min.js";
import dotenv from "dotenv";
import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import { ErrorBoundary } from "./components/error-handler/ErrorBoundary";
import "./index.css";
import reportWebVitals from "./reportWebVitals";
import { WinstonProvider } from "winston-react";
import winston from "winston";

dotenv.config({});
const { REACT_APP_DISABLE_LOGS } = process.env;

const transports = [];
if (["false", false].includes(REACT_APP_DISABLE_LOGS)) {
  transports.push(new winston.transports.Console());
}

const logger = winston.createLogger({
  transports,
});

ReactDOM.render(
  <WinstonProvider logger={logger}>
    <ErrorBoundary>
      <App />
    </ErrorBoundary>
  </WinstonProvider>,
  document.getElementById("root")
);

reportWebVitals();
