import { Fragment, useContext } from "react";
import { Checkbox } from "semantic-ui-react";
import { AllRobinContext } from "./AllRobins";

const RobinTable = (props) => {
  const robins = useContext(AllRobinContext);

  const { onClick, onSelect } = props;

  const handleClick = (event, index) => {
    event.stopPropagation();
    onClick(robins[index]);
  };
  // const handleDelete = (event, index) => {
  //   event.stopPropagation();
  // };

  const headers = ", Sno.,Name, Email , Phone, Type , Group , Created At"
    .split(",")
    .map((item) => item.trim())
    .map((header, index) => <th key={index}>{header}</th>);

  let rows = robins.map(
    (
      {
        selected,
        robin: {
          id,
          firstName,
          type,
          lastName,
          email,
          group,
          phone,
          createdAt,
        },
      },
      index
    ) => (
      <Fragment key={index}>
        <td onClick={() => onSelect(index)}>
          <Checkbox checked={selected} />
        </td>
        <td onClick={(event) => handleClick(event, index)}>{index + 1}</td>
        <td
          onClick={(event) => handleClick(event, index)}
        >{`${firstName} ${lastName}`}</td>
        <td onClick={(event) => handleClick(event, index)}>{email}</td>
        <td onClick={(event) => handleClick(event, index)}>{phone}</td>
        <td onClick={(event) => handleClick(event, index)}>{type}</td>
        <td onClick={(event) => handleClick(event, index)}>
          {group ? group.title : "No-Group"}
        </td>
        <td onClick={(event) => handleClick(event, index)}>
          {new Date(createdAt).toDateString()}
        </td>
      </Fragment>
    )
  );

  rows = rows.map((row, index) => <tr key={index}>{row}</tr>);

  return (
    <div className="table-responsive">
      <table
        style={{ cursor: "pointer" }}
        className="table table-hover table-striped table-bordered"
      >
        <thead>
          <tr>{headers}</tr>
        </thead>
        <tbody>{rows}</tbody>
      </table>
    </div>
  );
};

export { RobinTable };
