import React, { Fragment, useContext, useEffect, useState } from "react";
import { Tab } from "semantic-ui-react";
import { AppContext } from "../../App";
import { getAllDrives } from "../../services/drive-service";
import { getAllRobins } from "../../services/robin-service";
import { reportsPageTab } from "./config";
const ReportContext = React.createContext();
function Reports() {
  const [robins, setRobins] = useState([]);
  const { drives } = useContext(AppContext);

  useEffect(() => {
    getAllRobins().then((robins) => {
      console.log({ robins });
      setRobins(robins || []);
    });
  }, []);

  return (
    <Fragment>
      <ReportContext.Provider value={{ drives, robins }}>
        <div className="mt-3">
          <Tab panes={reportsPageTab} renderActiveOnly={false} />
        </div>
      </ReportContext.Provider>
    </Fragment>
  );
}

export { Reports, ReportContext };
