import { RobinCards } from "./RobinCards";
import { RobinTable } from "./RobinTable";

const ViewSwitcher = ({ view, onSelect, onClick }) => {
  const views = {
    table: <RobinTable onSelect={onSelect} onClick={onClick}></RobinTable>,
    cards: <RobinCards onSelect={onSelect} onClick={onClick}></RobinCards>,
  };

  return views[view];
};

export { ViewSwitcher };
