import { axios } from "./axios";

const getAllDrives = () => {
  return axios.get("/api/drives").then(({ data }) => data && data.drives);
};

const deleteDrive = (slug) => {
  return axios.delete(`/api/drives/${slug}`);
};

const saveDrive = (data) => {
  return axios.post(`/api/drives`, data);
};

export { getAllDrives, deleteDrive, saveDrive };
