import { Card, Divider, Header, Image, Segment } from "semantic-ui-react";
const DashboardOptions = ({ dashBoardCardOptions, onCardClick }) => {
  const options = dashBoardCardOptions;

  const createOptions = () => {
    return options.map(({ title, url, icon }, index) => (
      <div className="mx-auto  col-lg-3 col-md-4 col-sm-4 col-4" key={index}>
        <div className="mt-4 ">
          <Card className="mx-auto" onClick={() => onCardClick(url)}>
            <Image src={icon} wrapped ui={false} className="p-4" />
            <Card.Content>
              <Card.Header>
                <Header
                  style={{ fontSize: "1.3em" }}
                  className="text-center d-none d-sm-block"
                >
                  <span>{title}</span>
                </Header>
                <Header
                  style={{ fontSize: "0.8em" }}
                  className="text-center mt-4 d-block d-sm-none"
                >
                  <span>{title}</span>
                </Header>
              </Card.Header>
              <Card.Meta>
                <span hidden className="date">
                  Joined in 2015
                </span>
              </Card.Meta>
              <Card.Description></Card.Description>
            </Card.Content>
          </Card>
        </div>
      </div>
    ));
  };

  const createMobilcards = () => {
    return (
      <div className="">
        {options.map(({ title, url, icon }, index) => (
          <Segment onClick={() => onCardClick(url)} key={index} size="large">
            <Image className="float-end" src={icon} size="mini" avatar />
            <span style={{ fontSize: "1.3em" }}>
              {index + 1}. &nbsp;
              {title}
            </span>
          </Segment>
        ))}
      </div>
    );
  };
  return (
    <div className="conatiner">
      <div className="text-center mt-4 col-lg-7 mx-auto">
        <h1 className="display-6">Welcome to Dashboard</h1>
        <Divider />
      </div>
      {/* <div className="d-none d-sm-block"> */}
      <div className="container">
        <div className="mt-4 row">{createOptions()}</div>
      </div>

      {/* <div className="mt-4 d-block d-sm-none">{createMobilcards()}</div> */}
    </div>
  );
};

export { DashboardOptions };
