import { Fragment, useContext } from "react";
import { useState } from "react";
import { Divider, Form } from "semantic-ui-react";
import { AppContext } from "../../../App";
import { saveDrive } from "../../../services/drive-service";
import { MessageDialog } from "../common/MessageDialog";
import { GreenButton } from "../GreenButton";

const DriveForm = () => {
  const { setDrives, drives } = useContext(AppContext);
  const [formData, setFormData] = useState({ drive: "" });
  const [dialogData, setDialogData] = useState({
    show: false,
    body: "Please Add",
    title: "Message",
  });

  const handleFormSubmit = (event) => {
    event.preventDefault();

    if (!validateForm()) return;

    saveDrive(formData)
      .then((response) => {
        return onDriveSave(response.data.drive);
      })
      .catch((error) => {
        console.log({ error });
        return onDriveSaveError(error?.response?.data.message);
      });
  };

  const validateForm = () => {
    if (!formData.drive || formData.drive.trim().length < 2) {
      changeDialogState({
        show: true,
        isError: true,
        body: "Drive must be more then 2 character long.",
        title: "Error",
      });
      return;
    }

    return true;
  };

  const onDriveSave = (drive) => {
    setDrives([...drives, drive]);
    changeDialogState({
      show: true,
      isError: false,
      body: <p>{drive.drive} Saved sucessfully</p>,
      title: "Drive Saved!",
    });

    setFormData({ drive: "" });
    return;
  };

  const onDriveSaveError = (message) => {
    return changeDialogState({
      show: true,
      isError: true,
      body: message,
      title: "Error",
    });
  };

  const onInputChange = (event) => {
    const {
      target: { name, value },
    } = event;

    setFormData({ [name]: value });
  };

  const changeDialogState = (state) => {
    setDialogData({ ...dialogData, ...state });
  };

  const { drive } = formData;
  const divClasses = `col-lg-6 mx-auto border p-4 shadow shadow-sm rounded`;
  return (
    <Fragment>
      {/* Message Dialog  */}
      <MessageDialog
        title={dialogData.title}
        show={dialogData.show}
        isError={dialogData.isError}
        onOk={() => changeDialogState({ show: false })}
      >
        <p>{dialogData.body}</p>
      </MessageDialog>
      {/* conatiner... */}
      <div className="conatiner p-4">
        <div className="mt-4">
          <div className={divClasses}>
            <div className="">
              <div>
                <p className="display-6">Add New Drive</p>
                <Divider></Divider>
              </div>
              <Form onSubmit={handleFormSubmit}>
                <Form.Field>
                  <label>Drive Title</label>
                  <input
                    required
                    placeholder="Title"
                    onChange={onInputChange}
                    value={drive}
                    name="drive"
                  />
                </Form.Field>
                <br />
                <GreenButton type="submit">Save Drive</GreenButton>
              </Form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export { DriveForm };
