import { useEffect, useState } from "react";
import { Form, Divider } from "semantic-ui-react";
import { createEvent } from "../../../services/event-service";
import {
  formatCurrentTimeInHHMM,
  formatTodayDateInYYYYMMDD,
} from "../../../utils/format";
import { GreenButton } from "../GreenButton";
// drive, title, description, date, location, slug, deleted, _id, updatedAt, createdAt, __v, id
const Schedule = ({ drives }) => {
  const initialFormState = {
    title: "",
    description: "",
    location: "",
    date: formatTodayDateInYYYYMMDD(),
    time: formatCurrentTimeInHHMM(),
  };

  const [form, setForm] = useState({ ...initialFormState });
  const [showMessage, setShowMessage] = useState(false);

  useEffect(() => {
    if (showMessage) {
      console.log("Message Showing...");
      setTimeout(() => setShowMessage(false), 3000);
    }
  }, [showMessage]);

  const onInputChange = (event, data) => {
    const { name, value } = event.target;
    console.log(name, value, data);
    if (!data) {
      setForm({ ...form, [name]: value });
    } else {
      const { value, name } = data;
      setForm({ ...form, [name]: value });
    }
  };

  const scheduleDrive = () => {
    console.log(form);

    const time = form.time;
    if (time.split(":").length !== 2) {
      alert("Time Invalid");
    }
    const hr = +time.split(":")[0];
    const min = +time.split(":")[1];
    const event = { ...form, time: { hr, min } };

    console.log(event);

    createEvent(event).then((event) => {
      console.log({ created: event });
      setForm(initialFormState);
      setShowMessage(true);
    });
  };
  return (
    <div className="col-lg-6 mx-auto m-4">
      <div>
        <p className="display-6 p-0 m-0">Schedule A Drive</p>
        <Divider></Divider>
      </div>

      {showMessage && (
        <p className="alert alert-success border">Event Scheduled.</p>
      )}

      <div className="mt-4">
        <Form className="" onSubmit={scheduleDrive}>
          <Form.Input
            onChange={onInputChange}
            fluid={true}
            label="Title"
            placeholder="Title Of Event"
            name="title"
            value={form.title}
          />
          <Form.TextArea
            onChange={onInputChange}
            label="Description"
            placeholder="Description Of Event"
            name="description"
            value={form.description}
          />
          <Form.TextArea
            onChange={onInputChange}
            label="Location"
            name="location"
            required
            placeholder="Location"
            value={form.location}
          />

          <Form.Field>
            <label>Date</label>
            <input
              onChange={onInputChange}
              type="date"
              required
              value={form.date}
              name="date"
            />
          </Form.Field>
          <Form.Field>
            <label>Time</label>
            <input
              onChange={onInputChange}
              type="time"
              required
              value={form.time}
              name="time"
            />
          </Form.Field>

          <div className="mt-4">
            <Form.Field onChange={onInputChange}>
              <GreenButton type="submit"> Schedule Drive</GreenButton>
            </Form.Field>
          </div>
        </Form>
      </div>
    </div>
  );
};

export { Schedule };
