import { sub } from "date-fns";
import { isEmpty, isEqual } from "lodash";
import { useContext, useEffect, useState } from "react";
import { Form, Message } from "semantic-ui-react";
import { AppContext } from "../../../App";
import { usePrevious } from "../../../hooks";
import { getAllEvents } from "../../../services/event-service";
import { getAttendanceReport } from "../../../services/report-service";
import { formatDateInYYYYMMDD, formatWithAMandPM } from "../../../utils/format";
import { ReportContext } from "../../pages/Reports";
import { AttendanceReportTable } from "./AttendanceReportTable";

function AttendanceReport() {
  const selectedEventInitialValue = { _id: "" };
  const selectedRobinInitialValue = { _id: "" };
  const { drives } = useContext(AppContext);
  const { robins } = useContext(ReportContext);
  const [events, setEvents] = useState([]);
  const [report, setReport] = useState([]);
  const [transformedReport, setTransformedReport] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState(selectedEventInitialValue);
  const [selectedRobin, setSelectedRobin] = useState(selectedRobinInitialValue);
  const [filter, setFilter] = useState({
    date: formatDateInYYYYMMDD(sub(new Date(), { days: 4 })),
  });
  const [robinEventFilter, setRobinEventFilter] = useState({});
  const prevFilter = usePrevious(filter);
  const prevRobinEvent = usePrevious(robinEventFilter);

  useEffect(() => {
    if (isEqual(prevFilter, filter)) return;
    getAllEvents(filter).then((events) => {
      console.log({ events });
      setEvents(events);
    });
  }, [filter]);

  useEffect(() => {
    console.log("Report Changed..");
    const transformed = report
      .map((item) => ({
        ...item,
        event: isEqual(selectedEvent, selectedEventInitialValue)
          ? item.event
          : selectedEvent,
      }))
      .sort(
        ({ event: x }, { event: y }) =>
          new Date(y.date).getTime() - new Date(x.date).getTime()
      );
    setTransformedReport(transformed);
    console.log({ transformed });
  }, [report]);

  useEffect(() => {
    if (isEqual(prevRobinEvent, robinEventFilter)) return;
    if (isEmpty(robinEventFilter)) return;

    console.log("USE EFFECT ROBIN EVENT");
    getAttendanceReport(robinEventFilter).then((report) => {
      setReport(report);
    });
  }, [robinEventFilter]);

  const onInputChange = (event) => {
    const { name, value } = event.target;
    console.log({ name, value });
    const newFilter = { ...filter, [name]: value };
    setFilter(newFilter);
  };

  const handleEventChange = ({ target: { name, value } }) => {
    if (value) {
      const selectedEvent = events.find((event) => event._id === value);
      setSelectedEvent(selectedEvent);
    } else {
      setSelectedEvent({ _id: "" });
    }

    setRobinEventFilter({ ...robinEventFilter, event: value });
  };
  const handleRobinChange = ({ target: { name, value } }) => {
    if (value) {
      const selectedRobin = robins.find((robin) => robin._id === value);
      setSelectedRobin(selectedRobin);
    } else {
      setSelectedRobin({ _id: "" });
    }
    setRobinEventFilter({ ...robinEventFilter, robin: value });
  };

  const formatDateAndTimeForOption = (event) => {
    return `${new Date(event.date).toDateString()} ${formatWithAMandPM(
      event.time.hr,
      event.time.min
    )}`;
  };

  return (
    <section>
      <h1>Attendance Report </h1>
      <Form className="row">
        <div className="mt-3 col">
          <Form.Field>
            <label>Select Date</label>
            <input
              type="date"
              name="date"
              value={filter.date}
              onChange={onInputChange}
            />
          </Form.Field>
        </div>
        <div className="mt-3 col">
          <Form.Field>
            <label>Select Robin</label>
            <select
              value={selectedRobin._id}
              onChange={handleRobinChange}
              name="robin"
              id="robin"
            >
              <option value="">Select Robin</option>
              {robins.map(({ _id, firstName, lastName }) => (
                <option key={_id} value={_id}>
                  {`${firstName} ${lastName}`}
                </option>
              ))}
            </select>
          </Form.Field>
        </div>
        <div className="mt-3 col">
          <Form.Field>
            <label>Select Date</label>
            <select
              value={selectedEvent._id}
              onChange={handleEventChange}
              name="event"
              id="event"
            >
              <option value="">Select Event</option>
              {events.map((event) => (
                <option key={event._id} value={event._id}>
                  {formatDateAndTimeForOption(event)}
                </option>
              ))}
            </select>
          </Form.Field>
        </div>
      </Form>
      {!selectedEvent._id && !selectedRobin._id && (
        <Message color="blue">
          <h4>Select Robin or Event !</h4>
        </Message>
      )}
      <AttendanceReportTable {...{ report: transformedReport }} />
    </section>
  );
}

export { AttendanceReport };
