import { Table, Checkbox, Message } from "semantic-ui-react";
import toTitleCase from "titlecase";

const AttendanceMarker = ({
  robins,
  drives,
  onMarkAttendance,
  attendances,
}) => {
  const isPresent = (drive, robin) => {
    if (!attendances) return false;
    const attendanceOfRobin = attendances.find((at) => at.robin === robin._id);
    let result = false;

    if (attendanceOfRobin) {
      result = attendanceOfRobin[drive._id];
    }

    return !!result;
  };

  if (robins.length < 1) {
    return (
      <Message info>
        <h1 className="text-center">No Robins In Selected Group</h1>
      </Message>
    );
  }

  return (
    <div className="border rounded">
      <Table celled padded>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell>#</Table.HeaderCell>
            <Table.HeaderCell>Name</Table.HeaderCell>
            <Table.HeaderCell>Email</Table.HeaderCell>
            <Table.HeaderCell>Phone</Table.HeaderCell>
            <Table.HeaderCell>Group</Table.HeaderCell>
            {drives.map((drive) => (
              <Table.HeaderCell key={drive._id}>{drive.drive}</Table.HeaderCell>
            ))}
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {robins.map((robin, index) => (
            <Table.Row key={robin._id}>
              <Table.Cell>{index + 1}</Table.Cell>
              <Table.Cell>
                {toTitleCase(`${robin.firstName} ${robin.lastName}`)}
              </Table.Cell>
              <Table.Cell>{robin.email}</Table.Cell>
              <Table.Cell>{robin.phone}</Table.Cell>
              <Table.Cell>
                {toTitleCase(
                  robin.group ? `${robin?.group?.title}` : "No-group"
                )}
              </Table.Cell>
              {drives.map((drive) => (
                <Table.Cell key={drive._id}>
                  <Checkbox
                    checked={isPresent(drive, robin)}
                    onChange={(event, data) =>
                      onMarkAttendance({ event, data, drive, robin, index })
                    }
                    toggle
                  />
                </Table.Cell>
              ))}
            </Table.Row>
          ))}
        </Table.Body>
      </Table>
    </div>
  );
};

export { AttendanceMarker };
