import { appendQueryString } from "../utils";
import { axios } from "./axios";

const createEvent = (event) => {
  return axios
    .post("/api/events/", { ...event })
    .then((response) => response.data && response.data.event);
};
const getAllEvents = (query) => {
  const url = appendQueryString("/api/events/", query);
  return axios
    .get(url)
    .then((response) => response.data && response.data.events);
};

const getUpcomingEvents = () => {
  return axios
    .get("/api/events/upcoming/")
    .then((response) => response.data && response.data.events);
};
const getDoneEvents = () => {
  return axios
    .get("/api/events/done/")
    .then((response) => response.data && response.data.events);
};

export { createEvent, getUpcomingEvents, getDoneEvents, getAllEvents };
