import { Modal } from "react-bootstrap";
import { GreenButton } from "../GreenButton";
const MessageDialog = ({ show, button, title, children, onOk, isError }) => {
  return (
    <Modal show={show}>
      <Modal.Header>
        <Modal.Title>
          {title || "Message"}{" "}
          {isError && <img height="30px" src="/icons/cancel.png" alt="" />}
          {!isError && <img height="30px" src="/icons/info.png" alt="" />}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>{children || "please add body to Modal."}</Modal.Body>
      <Modal.Footer>
        <GreenButton onClick={onOk}>{button || "Ok"}</GreenButton>
      </Modal.Footer>
    </Modal>
  );
};

export { MessageDialog };
