const getIn2Digit = (number) => {
  if (number < 10) return `0${number}`;
  return number;
};

const formatTodayDateInYYYYMMDD = () => {
  const now = new Date();
  return formatDateInYYYYMMDD(now);
};
const formatDateInYYYYMMDD = (date) => {
  date = new Date(date);

  if (!date.getTime()) return null;

  const year = date.getFullYear();
  const month = getIn2Digit(date.getMonth() + 1);
  const _date = getIn2Digit(date.getDate());
  return `${year}-${month}-${_date}`;
};

const formatCurrentTimeInHHMM = () => {
  const now = new Date();
  const hh = now.getHours();
  const mm = now.getMinutes();

  return `${getIn2Digit(hh)}:${getIn2Digit(mm)}`;
};

const formatWithAMandPM = (hr, min) => {
  if ((hr < 0 && min < 0) || !hr || !min) {
    return null;
  }
  const date = new Date(0, 0, 0, hr, min, 0, 0);
  return date.toLocaleTimeString("en-us", {
    hour12: true,
    hour: "2-digit",
    minute: "2-digit",
  });
};

export {
  formatTodayDateInYYYYMMDD,
  formatCurrentTimeInHHMM,
  formatWithAMandPM,
  formatDateInYYYYMMDD,
};
