import { Fragment, useEffect } from "react";
import { useState } from "react";
import { Divider, Form } from "semantic-ui-react";
import { getAllGroups } from "../../../services/group-service";
import { createRobin } from "../../../services/robin-service";
import { MessageDialog } from "../common/MessageDialog";
import { GreenButton } from "../GreenButton";

const RobinForm = () => {
  const formFieldOfTypeTextArea = [
    {
      placeholder: "Address",
      name: "address",
      label: "Address",
      required: true,
      rows: 3,
    },
  ];

  const formFieldOfTypeSelect = [
    {
      placeholder: "Group",
      name: "group",
      label: "Select Group",
      required: true,
    },
  ];

  const formFieldsOfTypeInput = [
    {
      type: "text",
      placeholder: "First Name",
      name: "firstName",
      label: "First Name",
      required: true,
    },
    {
      type: "text",
      placeholder: "Last Name",
      name: "lastName",
      label: "Last Name",
      required: true,
    },
    {
      type: "email",
      placeholder: "Email",
      name: "email",
      label: "Email Address",
      required: true,
    },
    {
      type: "password",
      placeholder: "Password",
      name: "password",
      label: "Password",
      required: true,
    },
    {
      type: "text",
      placeholder: "Phone",
      name: "phone",
      label: "Phone",
      required: true,
    },
    {
      type: "text",
      placeholder: "Profession",
      name: "profession",
      label: "Profession",
      required: true,
    },
    {
      type: "date",
      placeholder: "Date Of Birth",
      name: "DOB",
      label: "Date Of Birth",
      required: false,
    },
  ];

  const [groups, setGroups] = useState([]);

  // fetch all groups for form
  useEffect(() => {
    getAllGroups().then((groups) => {
      setGroups(groups);
    });
  }, []);

  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    profession: "",
    DOB: "2000-01-02",
    address: "",
    group: "-1",
    password: "",
  });
  const [dialogData, setDialogData] = useState({
    show: false,
    body: "Please Add",
    title: "Message",
  });

  const handleFormSubmit = (event) => {
    event.preventDefault();

    if (!validateForm()) return;

    createRobin(formData).then((response) => {
      const { robin } = response.data;
      const temp = {};
      for (let key in formData) {
        temp[key] = "";
      }
      setFormData({ ...temp });
      setDialogData({
        ...dialogData,
        show: true,
        isError: false,
        title: "Success",
        body: `${robin.firstName} Created SuccessFuly.`,
      });
    });
  };

  function validateEmail(email) {
    const re =
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  const validateForm = () => {
    const { firstName, lastName, email, group } = formData;

    console.log({ group });

    let message = "";
    if (firstName.length < 5) {
      message = "First Name must be 5 or more character long.";
    } else if (lastName.length < 2) {
      message = "First Name must be 2 or more character long.";
    } else if (!validateEmail(email)) {
      message = "Invalid Email";
    } else if (group === "-1") {
      console.log({ group: "invalid" });
      message = "Please Select A Group";
    }
    if (message) {
      changeDialogState({
        show: true,
        isError: true,
        body: message,
        title: "Error",
      });

      return false;
    }

    return true;
  };

  const onInputChange = (event) => {
    const {
      target: { name, value },
    } = event;
    console.log(name, value);
    console.log(formData);
    setFormData({ ...formData, [name]: value });
  };

  const changeDialogState = (state) => {
    setDialogData({ ...dialogData, ...state });
  };

  const divClasses = `col-lg-6 mx-auto border p-4 shadow shadow-sm rounded`;

  const getAllFormFields = () => {
    const inputTypesText = formFieldsOfTypeInput.map(
      ({ type, placeholder, name, required, label }, index) => (
        <Form.Field key={index}>
          <label>{label}</label>
          <input
            required={required}
            placeholder={placeholder}
            onChange={onInputChange}
            type={type}
            value={formData[name]}
            name={name}
          />
        </Form.Field>
      )
    );

    const textAreas = formFieldOfTypeTextArea.map(
      ({ label, required, name, placeholder, rows }, index) => (
        <Form.Field key={`textarea-${index}`}>
          <label>{label}</label>
          <textarea
            required={required}
            placeholder={placeholder}
            onChange={onInputChange}
            name={name}
            rows={rows}
            value={formData[name]}
          ></textarea>
        </Form.Field>
      )
    );

    const selects = formFieldOfTypeSelect.map(
      ({ label, name, options, placeholder, required }, index) => (
        <Form.Field key={Math.random() * 10000}>
          <label>{label}</label>
          <select
            required={required}
            placeholder={placeholder}
            onChange={onInputChange}
            name={name}
          >
            <option key={-1} value="-1">
              Select Group
            </option>
            {groups.map(({ title, _id }, index) => (
              <option key={index} value={_id}>
                {title}
              </option>
            ))}
          </select>
        </Form.Field>
      )
    );

    return [...inputTypesText, ...textAreas, ...selects];
  };

  return (
    <Fragment>
      {/* Message Dialog  */}
      <MessageDialog
        title={dialogData.title}
        show={dialogData.show}
        isError={dialogData.isError}
        onOk={() => changeDialogState({ show: false })}
      >
        <p>{dialogData.body}</p>
      </MessageDialog>
      {/* container... */}
      <div className="container p-4">
        <div className="mt-4">
          <div className={divClasses}>
            <div className="">
              <div>
                <p className="display-6">Add A Robin</p>
                <Divider></Divider>
              </div>
              <Form onSubmit={handleFormSubmit}>
                {getAllFormFields()}

                <br />
                <GreenButton type="submit">Save Robin</GreenButton>
              </Form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export { RobinForm };
