import { useContext, useEffect, useState } from "react";
import toTitleCase from "titlecase";
import { formatDateInYYYYMMDD, formatWithAMandPM } from "../../../utils/format";
import { ReportContext } from "../../pages/Reports";

const AttendanceReportTable = (props) => {
  const { report } = props;
  const { drives, robins } = useContext(ReportContext);
  const [values, setValues] = useState([]);
  const [headers, setHeaders] = useState(
    "Sno.,Event,Robin".split(",").map((header) => header.trim())
  );

  const isPresent = (report, robin, drive) => {
    return report.attendanceInfo
      ? report.attendanceInfo.find((info) => info.drive._id == drive._id)
          ?.isPresent
      : false;
  };

  useEffect(() => {
    setHeaders([...headers, ...drives.map((drive) => drive.drive)]);
  }, [drives]);

  useEffect(() => {
    if (report) {
      const valuesArray = report.map((report, index) => {
        const { event, robin } = report;
        const { firstName, lastName } = robin;
        const { date, time, location } = event || {};
        const { hr, min } = time || {};

        const transformedToArray = [
          index + 1,
          <div>
            <p className="m-0">
              <span className="p-2"></span>{" "}
              {formatDateInYYYYMMDD(date) || "-"}
            </p>
            <p className="m-0">
              <span className="p-2"></span>{" "}
              {formatWithAMandPM(hr, min) || "-"}
            </p>
            <p className="m-0">
              <span className="p-2"></span> {toTitleCase(location)}
            </p>
          </div>,
          `${firstName || "-"} ${lastName || ""}`,
        ];

        drives.forEach((drive) => {
          // check is present or not
          transformedToArray.push(
            isPresent(report, robin, drive) ? (
              <p className="text-center"></p>
            ) : (
              <p className="text-center"></p>
            )
          );
        });

        return transformedToArray;
      });
      setValues(valuesArray);
    }
  }, [report]);

  return (
    <section id="table" className="mt-3">
      <div className="table-responsive">
        <table
          style={{ verticalAlign: "middle" }}
          className="table table-hover table-bordered table-striped table-light"
        >
          <thead>
            <tr>
              {headers.map((header) => (
                <th key={header}>{header}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {values.map((value, index) => (
              <tr key={index}>
                {value.map((v, innerIndex) => (
                  <td key={index + "" + innerIndex}>{v}</td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </section>
  );
};

export { AttendanceReportTable };
