import { useState } from "react";
import { CSVLink } from "react-csv";
import { Button } from "semantic-ui-react";
import {
  getAllDrives,
  getAllEvents,
  getAllGroups,
  getAllRobins,
} from "../../services/report-service";
const ExportDataPage = () => {
  const [tableRows, setTableRows] = useState([
    { title: "Robins", function: fetchAllRobins, data: [] },
    { title: "Groups", function: fetchAllGroups, data: [] },
    { title: "Drives", function: fetchAllDrives, data: [] },
    { title: "Events", function: fetchAllEvents, data: [] },
  ]);

  function updateRowData(title, data) {
    const copy = [...tableRows];
    const row = copy.find((row) => row.title.toLowerCase() === title);
    row.data = data;
    setTableRows(copy);
  }

  function fetchAllRobins() {
    getAllRobins().then((robins) => {
      const transformed = robins.map((robin) => {
        const updated = { ...robin };
        updated.group = robin.group ? robin.group.title : "No-group";
        updated.DOB = new Date(robin.DOB).toDateString();
        return updated;
      });
      updateRowData("robins", transformed);
    });
  }
  function fetchAllDrives() {
    getAllDrives().then((drives) => {
      updateRowData("drives", drives);
    });
  }
  function fetchAllGroups() {
    getAllGroups().then((groups) => {
      updateRowData("groups", groups);
    });
  }
  function fetchAllEvents() {
    getAllEvents().then((events) => {
      updateRowData("events", events);
    });
  }

  return (
    <div className="container">
      <br />
      <div>
        <h1 className="display-5">Export Data</h1>
      </div>
      <hr />
      <div>
        <table className="table table-hover table-striped table-bordered ">
          <thead>
            <tr>
              <th>#</th>
              <th>Report Of</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {tableRows.map((row, index) => (
              <tr key={`tr-${index}`}>
                <td>{index + 1}</td>
                <td>{row.title}</td>
                <td>
                  <Button onClick={row.function} size="mini" color="green">
                    Export
                  </Button>

                  {row.data.length ? (
                    <CSVLink
                      filename={`${row.title}-${new Date().toDateString()}.csv`}
                      target="_blank"
                      data={row.data}
                    >
                      <Button size="mini" color="green">
                        Download
                      </Button>
                    </CSVLink>
                  ) : null}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export { ExportDataPage };
