import { Fragment, useContext } from "react";
import { useState } from "react";
import { useHistory } from "react-router-dom";
import { Form, Header, Divider } from "semantic-ui-react";
import { loginRobin } from "../../services/robin-service";
import { MessageDialog } from "../ui/common/MessageDialog";
import { GreenButton } from "../ui/GreenButton";
import { saveToken } from "../../services/token-service";
import { AuthContext } from "../../App";

const Login = (props) => {
  const AFTER_LOGIN_URL = "/dashboard";
  const history = useHistory();

  const { onLogin } = useContext(AuthContext);

  const [formValues, setFormValues] = useState({
    email: "",
    password: "",
  });

  const [dailogInfo, setDialogInfo] = useState({
    show: false,
  });

  const onInputChange = ({ target: { name, value } }) => {
    setFormValues({ ...formValues, [name]: value });
  };

  const isFormValid = () => {
    return true;
  };

  const handleFormSubmit = (event) => {
    event.preventDefault();

    if (!isFormValid()) {
      alert("Form Is Not Valid");
      return;
    }

    loginRobin({ ...formValues }).then(({ data: { token, type } }) => {
      saveToken({ token, type });
      history.push(AFTER_LOGIN_URL);
      onLogin();
    });
  };

  const { email, password } = formValues;
  return (
    <Fragment>
      <MessageDialog
        show={dailogInfo.show}
        title={dailogInfo.title}
        isError={dailogInfo.isError}
        onOk={() => setDialogInfo({ ...dailogInfo, show: false })}
      >
        {dailogInfo.body}
      </MessageDialog>
      <div className="container">
        <div className="col-lg-4 col-md-7 mx-auto mt-4">
          <div className=" border p-4 rounded mt-4 p-4">
            {<Header as="h1">Login Here</Header>}
            <Divider></Divider>
            <Form onSubmit={handleFormSubmit}>
              <Form.Field>
                <label>Email Address</label>
                <input
                  required
                  placeholder="Email Address"
                  onChange={onInputChange}
                  value={email}
                  name="email"
                />
              </Form.Field>
              <Form.Field>
                <label>Password</label>
                <input
                  required
                  placeholder="Password"
                  onChange={onInputChange}
                  name="password"
                  type="password"
                  value={password}
                />
              </Form.Field>
              <br />
              <GreenButton type="submit">Login</GreenButton>
            </Form>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export { Login };
