import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import { Layout } from "./components/layout/Layout";
import { DashBoard } from "./components/pages/Dashboard";
import { Home } from "./components/pages/Home";
import { Login } from "./components/pages/Login";
import { Register } from "./components/pages/Register";
import { PrivateRoute } from "./components/private-route/PrivateRoute";
import { getAllDrives } from "./services/drive-service";
import { getDecodedToken } from "./services/token-service";

const path = require("path");

const AuthContext = React.createContext();
const AppContext = React.createContext();

function App() {
  // alert(process.env.FAST_REFRESH);

  const [authContext, setAuthContext] = useState({
    onLogin,
    onLogout,
    loggedInUser: false,
  });

  const [drives, setDrives] = useState([]);

  useEffect(() => {
    const payload = getDecodedToken();
    setAuthContext({ ...authContext, loggedInUser: payload });
  }, []);
  useEffect(() => {
    getAllDrives().then((drives) => {
      setDrives(drives);
    });
  }, []);

  function onLogin() {
    setAuthContext({ ...authContext, loggedInUser: getDecodedToken() });
  }
  function onLogout() {
    setAuthContext({ ...authContext, loggedInUser: null });
  }

  return (
    <AppContext.Provider value={{ drives, setDrives }}>
      <AuthContext.Provider value={authContext}>
        <Router>
          <Layout></Layout>

          <Switch>
            <Route path="/login">
              <Login />
            </Route>

            <Route path="/register">
              <Register />
            </Route>

            <PrivateRoute path="/dashboard" onlyFor="admin">
              <DashBoard></DashBoard>
            </PrivateRoute>

            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Router>
      </AuthContext.Provider>
    </AppContext.Provider>
  );
}

export default App;
export { AuthContext, AppContext };
