import { useEffect, useState } from "react";
import { getDoneEvents } from "../../../services/event-service";
import { EventTable } from "./EventTable";

const Done = () => {
  const [events, setEvents] = useState([]);
  useEffect(() => {
    getDoneEvents().then((events) => {
      setEvents(events);
    });
  }, []);
  return (
    <div>
      <EventTable events={events} />
    </div>
  );
};

export { Done };
