import { axios } from "./axios";

const markAttendance = (attendanceData) => {
  console.log({ attendanceData });

  return axios
    .post("/api/attendances/", attendanceData)
    .then(({ data }) => data && data.attendance);
};

const getAttendanceOfEvent = (eventId) => {
  return axios
    .get(`/api/attendances/event/${eventId}`)
    .then(({ data }) => data && data.attendances);
};

export { markAttendance, getAttendanceOfEvent };
