import React from "react";
import { useEffect, useState } from "react";
import { Tab } from "semantic-ui-react";
import { toTitleCase } from "titlecase";
import { getAllDrives } from "../../services/drive-service";
import { formatTodayDateInYYYYMMDD, formatWithAMandPM } from "../../utils";
import { Done } from "../ui/schedule-drive/Done";
import { Schedule } from "../ui/schedule-drive/Schedule";
import { Upcoming } from "../ui/schedule-drive/Upcoming";

let ScheduleDriveContext = React.createContext({});

const ScheduleDrive = (props) => {
  const [drives, setDrives] = useState([]);
  const host = window?.location?.origin;

  useEffect(() => {
    getAllDrives().then((drives) => {
      console.log(drives);
      setDrives([...drives]);
    });
  }, []);

  const handleEventWhatsAppShare = (event) => {
    console.log("Sharing On WhatsApp");
    console.log({ event });
    let message = [
      `A Drive is Scheduled `,
      `* Date:* ${new Date(event.date).toDateString()}`,
      `* Time:* ${formatWithAMandPM(event.time.hr, event.time.min)}`,
      `* Location:* ${event.location}`,
      `* Scheduled By:* ${event.createdBy.firstName} ${event.createdBy.lastName}`,
    ]
      .map((e) => toTitleCase(e))
      .concat([`For More Visit: ${host}`])
      .join("\n");

    const data = { text: message };
    const text = new URLSearchParams(data).toString();
    const url = `https://api.whatsapp.com/send?${text}`;
    window.open(url, "_black");
  };

  const panes = [
    {
      menuItem: "Schedule",
      render: () => (
        <Tab.Pane attached={false}>
          <Schedule drives={drives} />
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Upcoming",
      render: () => (
        <Tab.Pane attached={false}>
          <Upcoming />
        </Tab.Pane>
      ),
    },
    {
      menuItem: "Done",
      render: () => (
        <Tab.Pane attached={false}>
          <Done />
        </Tab.Pane>
      ),
    },
  ];

  const context = {
    drives,
    handleEventWhatsAppShare,
  };

  return (
    <div className="mt-3 mx-auto">
      <ScheduleDriveContext.Provider value={context}>
        <Tab menu={{ secondary: true, pointing: true }} panes={panes} />
      </ScheduleDriveContext.Provider>
    </div>
  );
};

export { ScheduleDrive, ScheduleDriveContext };
