import { List, Image, Form, Message } from "semantic-ui-react";
import { Modal } from "react-bootstrap";
import { GreenButton } from "../../GreenButton";
import {
  formatTodayDateInYYYYMMDD,
  formatWithAMandPM,
} from "../../../../utils";
import toTitleCase from "titlecase";
import { useState } from "react";

const EventSelector = ({
  events,
  onSelect,
  show,
  onClose,
  selectedEvent,
  onEventFilterChange,
}) => {
  const [selectedDate, setSelectedDate] = useState(
    formatTodayDateInYYYYMMDD(new Date())
  );
  const [errorMessage, setErrorMessage] = useState("");
  function onDateChange({ target: { name, value } }) {
    const date = new Date(value);
    if (isNaN(date.getTime())) {
      setErrorMessage("Invalid Date");
      return;
    }

    setSelectedDate(value);
    setErrorMessage("");

    onEventFilterChange({ date: value });
  }

  function renderList() {
    if (!events.length) {
      return (
        <Message warning>
          <h3>No Events Scheduled On {selectedDate}</h3>
        </Message>
      );
    }
    return (
      <List celled>
        {events.map((event, index) => (
          <List.Item
            key={index}
            className="hand"
            style={{
              backgroundColor: selectedEvent === event ? "#f0eee9" : "",
            }}
            onClick={() => onSelect(event)}
          >
            <Image avatar src="/icons/event.png" />
            <List.Content>
              <List.Header>
                {new Date(event.date).toDateString()}
                {` `}
                {formatWithAMandPM(event.time.hr, event.time.min)}
              </List.Header>
              {toTitleCase(event.title)}
              <br></br>
              {toTitleCase(event.location)}
            </List.Content>
          </List.Item>
        ))}
      </List>
    );
  }

  return (
    <Modal show={show}>
      <Modal.Header>
        <Modal.Title>
          <h2>Select An Event</h2>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {errorMessage ? (
          <Message error>{errorMessage}</Message>
        ) : (
          <Message info>
            Showing Events Of {new Date(selectedDate).toLocaleDateString()}{" "}
          </Message>
        )}
        <Form>
          <Form.Field>
            <label>Select Date</label>
            <input onChange={onDateChange} value={selectedDate} type="date" />
          </Form.Field>
        </Form>
        {renderList()}
      </Modal.Body>
      <Modal.Footer>
        <GreenButton onClick={onClose}>Close</GreenButton>
      </Modal.Footer>
    </Modal>
  );
};

export { EventSelector };
