import { Fragment, useContext, useEffect, useState } from "react";
import { Button, Divider, Table } from "semantic-ui-react";
import { AppContext } from "../../../App";
import { deleteDrive, getAllDrives } from "../../../services/drive-service";
import { ConfirmationDialog } from "../common/ConfirmationDialog";

const AllDrives = () => {
  const { drives, setDrives } = useContext(AppContext);
  const [showModal, setShowModal] = useState(false);
  const [driveToDelete, setDriveToDelete] = useState(null);

  const handleDelete = () => {
    console.log("We are deleting", driveToDelete);
    deleteDrive(driveToDelete.slug).then((response) => {
      console.log(response);
      setShowModal(false);

      const updated = drives.filter((drive) => drive._id !== driveToDelete._id);
      setDrives(updated);
    });
  };

  const showModalToConfirm = (drive) => {
    setDriveToDelete(drive);
    setShowModal(true);
  };
  const changeModelState = (state) => {
    setShowModal(state);
  };

  const renderModel = () => {
    return (
      driveToDelete && (
        <ConfirmationDialog
          title="Confirm Deletion"
          show={showModal}
          positiveAction="Yes"
          positiveActionHandler={() => {
            handleDelete(driveToDelete);
          }}
          negativeAction="No"
          negativeActionHandler={() => {
            changeModelState(false);
          }}
        >
          <p>
            Do you really want to delete <b>{driveToDelete.drive}</b> Drive
          </p>
        </ConfirmationDialog>
      )
    );
  };

  const renderTableBody = () => {
    return (
      <Table.Body>
        {drives.map((drive, index) => (
          <Table.Row key={index}>
            <Table.Cell>{index + 1}</Table.Cell>
            <Table.Cell>
              <h4>{drive.drive}</h4>
            </Table.Cell>
            <Table.Cell>
              {drive.createdAt ? (
                <Fragment>
                  {new Date(drive.createdAt).toLocaleDateString()}{" "}
                  {new Date(drive.createdAt).toLocaleTimeString()}
                </Fragment>
              ) : (
                "Not-Available"
              )}
            </Table.Cell>

            <Table.Cell>{drive.createdBy || "Not-Available"}</Table.Cell>

            <Table.Cell>
              <Button
                onClick={() => showModalToConfirm(drive)}
                size="mini"
                color="red"
              >
                Delete
              </Button>
            </Table.Cell>
          </Table.Row>
        ))}
      </Table.Body>
    );
  };

  const rednerTableHeader = () => (
    <Table.Header>
      <Table.Row>
        <Table.HeaderCell>Sno.</Table.HeaderCell>
        <Table.HeaderCell>Drive Title</Table.HeaderCell>
        <Table.HeaderCell>Created At</Table.HeaderCell>
        <Table.HeaderCell>Created By</Table.HeaderCell>
        <Table.HeaderCell>Actions</Table.HeaderCell>
      </Table.Row>
    </Table.Header>
  );

  return (
    <div className="conatiner mt-4">
      <div className="col-lg-8 mx-auto">
        <div className="col-lg-12 mx-auto">
          <h1 className="display-6">All Drives</h1>
          <Divider></Divider>
        </div>

        <Table celled>
          {rednerTableHeader()}
          {renderTableBody()}
        </Table>
      </div>

      {renderModel()}
    </div>
  );
};

export { AllDrives };
