import { Fragment, useContext } from "react";
import { toTitleCase } from "titlecase";
import { Checkbox, Segment } from "semantic-ui-react";
import { AllRobinContext } from "./AllRobins";

const RobinCards = ({ onClick, onSelect }) => {
  const robins = useContext(AllRobinContext);
  return (
    <Fragment>
      <div className="row">
        {robins.map(
          (
            {
              selected,
              robin: { email, _id, firstName, lastName, phone, createdAt },
            },
            index
          ) => (
            <div
              onClick={() => onClick(robins[index])}
              className="col-lg-3 col-md-4 col-sm-5 col-11 mx-auto mt-3"
              key={_id}
            >
              <div className="card">
                <div className="card-body">
                  <div>
                    <div className="float-end" onClick={() => onSelect(index)}>
                      <Segment compact>
                        <Checkbox className="mr-4" checked={selected} />
                      </Segment>
                    </div>
                  </div>
                  <div className="">
                    <h5 className="card-title">
                      {toTitleCase(firstName)} {toTitleCase(lastName)}
                    </h5>
                  </div>
                  <p className="card-text">
                    <span>{toTitleCase(email)}</span>
                    <br />
                    <span>{toTitleCase(phone)}</span>
                    <br />
                    <span>{new Date(createdAt).toDateString()}</span>
                  </p>
                </div>
              </div>
            </div>
          )
        )}
      </div>
    </Fragment>
  );
};

export { RobinCards };
