import {
  formatTodayDateInYYYYMMDD,
  formatCurrentTimeInHHMM,
  formatWithAMandPM,
} from "./format";

const appendQueryString = (url = "", object) => {
  if (!object) return url;

  const keyValue = [];
  for (let key in object) keyValue.push([key, object[key]]);
  const queryString = new URLSearchParams(keyValue).toString();
  let separator = "?";

  if (url.includes("?") || url.includes("&")) separator = "&";

  return `${url}${separator}${queryString}`;
};

export {
  formatTodayDateInYYYYMMDD,
  formatCurrentTimeInHHMM,
  formatWithAMandPM,
  appendQueryString,
};
