import { useContext } from "react";
import { Icon } from "semantic-ui-react";
import { toTitleCase } from "titlecase";
import { formatWithAMandPM } from "../../../utils/format";
import { ScheduleDriveContext } from "../../pages/ScheduleDrive";

const EventTable = ({ events }) => {
  const { handleEventWhatsAppShare } = useContext(ScheduleDriveContext);

  return (
    <div className="table-responsive">
      <table className="table table-striped">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Date</th>
            <th scope="col">Time</th>
            <th scope="col">Created By</th>
            {/* <th scope="col">Title</th> */}
            {/* <th scope="col">Description</th> */}
            <th scope="col">Locations</th>
            <th scope="col">Actions</th>
          </tr>
        </thead>
        <tbody>
          {events.map((event, index) => (
            <tr key={event._id}>
              <td>{index + 1}</td>
              <td>{new Date(event.date).toDateString()}</td>
              <td>{formatWithAMandPM(event.time.hr, event.time.min)}</td>
              <td>{toTitleCase(event.createdBy?.firstName)}</td>
              {/* <td>{toTitleCase(event.title)}</td> */}
              {/* <td>{toTitleCase(event.description)}</td> */}
              <td>{toTitleCase(event.location)}</td>
              <td>
                <Icon
                  onClick={() => handleEventWhatsAppShare(event)}
                  name="whatsapp"
                  color="green"
                  size="large"
                  link
                ></Icon>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export { EventTable };
