const GreenButton = (props) => {
  const { REACT_APP_BRAND_COLOR } = process.env;
  const buttonStyle = {
    backgroundColor: REACT_APP_BRAND_COLOR,
    color: "white",
  };
  return (
    <button className="ui button" style={buttonStyle} {...props}>
      {props.children}
    </button>
  );
};

export { GreenButton };
