import decode_token from "jwt-decode";

const saveToken = ({ token, type }) => {
  localStorage.setItem("token", JSON.stringify({ token, type }));
};

const getToken = () => {
  const token = localStorage.getItem("token");

  return token ? `${JSON.parse(token).type} ${JSON.parse(token).token}` : null;
};

const getDecodedToken = () => {
  try {
    const token = getToken();
    if (token) {
      return decode_token(token);
    }
  } catch (err) {
    return null;
  }
};

export { getToken, saveToken, getDecodedToken };
