import { Fragment, useEffect, useState } from "react";
import { getUpcomingEvents } from "../../../services/event-service";
import { Card, Image, Icon, Divider } from "semantic-ui-react";

const UpcomingDrives = () => {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    getUpcomingEvents().then((events) => {
      console.log(JSON.stringify(events[0], null, 4));
      setEvents(
        events.map((event, index) => {
          event.image = `/images/thumbnail-${(index + 2) % 3}.png`;
          const eventDate = new Date(event.date);
          event.date = eventDate.toDateString();
          const today = new Date();
          if (eventDate.getDate() === today.getDate()) {
            event.date += " (Today)";
          }
          if (eventDate.getDate() === today.getDate() + 1) {
            event.date += " (Tomorrow)";
          }
          const {
            createdBy: { firstName, lastName },
          } = event;
          event.createdBy = `${firstName} ${lastName}`;
          event.time = new Date(
            0,
            0,
            0,
            event.time.hr,
            event.time.min
          ).toLocaleString("en-US", {
            hour: "numeric",
            minute: "2-digit",
            hour12: true,
          });
          return event;
        })
      );
    });
  }, []);
  return (
    <Fragment>
      <div>
        <span className="display-5">
          Upcoming Drives <Icon name="clock" color="black"></Icon>
        </span>

        <Divider></Divider>
      </div>
      <div className="row">
        {events.map((event, index) => (
          <div key={index} className="mt-4 col-12 col-lg-6">
            <Card fluid>
              <Image src={event.image} wrapped ui={false} />
              <Card.Content>
                <Card.Header>
                  <h3 className="m-0">{event.date}</h3>
                  <h4 className="mt-2">{event.time}</h4>
                </Card.Header>
                <Card.Meta>
                  <span className="date">Created By {event.createdBy}</span>
                </Card.Meta>
                <Card.Description>Location: {event.location}</Card.Description>
              </Card.Content>
              <Card.Content extra hidden>
                <a>
                  <Icon name="user" />
                  22 Friends
                </a>
              </Card.Content>
            </Card>
          </div>
        ))}
      </div>
    </Fragment>
  );
};

export { UpcomingDrives };
