import { axios } from "./axios";

const getAllGroups = () => {
  return axios
    .get("/api/groups/")
    .then((response) => response.data && response.data.groups);
};
const createGroup = (group) => {
  return axios
    .post("/api/groups/", group)
    .then((response) => response.data && response.data.group);
};
const getGroupById = (id) => {};
const deleteGroup = (id) => {
  return axios.delete(`/api/groups/${id}`);
};

export { getAllGroups, createGroup, deleteGroup, getGroupById };
